<template>
  <div>

    <div class="pagebar-title">新增主体</div>

    <div class="module">
      <div class="mtitle">主体名称</div>
      <el-input v-model="form.name" placeholder="主题名称, 农场名, 客户名 等"></el-input>
    </div>

    <div class="module">
      <el-button @click="submit()">提交</el-button>
    </div>

  </div>
</template>

<script>
// @ is an alias to /src
import { AddorEdit } from "@/api/principal";
export default {
  name: "Home",
  data() {
    return {
      form:{
        name: null,
      }
    };
  },
  components: {
  },
  methods: {
    submit() {
      this.$http(
        AddorEdit(this.form),
        (res) => {
          console.log(res);
          if (res.code == 200) {
            this.$message({
              message: res.msg,
              type: "success",
            });
          } else {
            this.$message({
              message: res.msg,
              type: "error",
              duration: 5 * 1000,
            });
          }
        }
      );
    },
  }
};
</script>
<style lang="scss" scoped>
</style>